  position: relative;
  right: 130px;
  margin: 1rem;
}
.Logo{
    width: 100%;
    /* display: inline-block;
   padding: 0; */
   position: absolute;
   margin: 1rem;
}

.logoS img{
margin: 0;
}
.sym{
  z-index: 4;
  width: 120px;
  right:17px;
  top: 35px;
  transition: 0.4s;
}

.logo1{
  z-index: 3;
  top: 0;
  transition: 0.2s;
}
.logo2{
z-index: 2;
top: 21px;
transition: 0.4s;


}
.logo3{
z-index: 1;
top: 41px;
transition: 0.4s;
}
.logoS:hover .logo3{
  top: 96px;
}
.logoS:hover .logo2{
  top: 51px;
}

.LogoSectionAbout{
    text-align: left;
    width: 80%;
    display: inline-block;
    position: relative;
    font-family: 'Nunito', sans-serif;
}
.LogoSectionAbout h1{
    font-weight: 700;
    text-align: left;
}


.Logo{

    -webkit-animation: mover 1s infinite  alternate;
    animation: mover 2s infinite  alternate;
   }
   @-webkit-keyframes mover {
     0% { transform: translateY(0); }
     100% { transform: translateY(-20px); }
   }
   @keyframes mover {
     0% { transform: translateY(0); }
     100% { transform: translateY(-20px); }
   }

   @media  (max-width: 570px){
    .logoS{
      margin-top: 10rem;
      margin-bottom: -10rem;
    }
    .Logo{
     margin: 0 auto;
    }
  }

  @media  (max-width: 570px){
